import qs from "qs";
import axios from "axios"; //引入axios

function request(url, method, data, blob) {
  switch (method) {
    case "get":
      return axios({
        method: "get",
        url,
        params: data
      });
    case "post":
      return axios({
        method: "post",
        url,
        data,
        responseType: blob || "" // 判断数据类型
      });
    case "delete":
      return axios({
        method: "delete",
        url,
        params: data
      });
    case "put":
      return axios({
        method: "put",
        url,
        data: qs.stringify(data)
      });
    default:
      return axios;
  }
}

export default request;
