<template>
  <div
    class="carousel"
    @mouseover="stopAuto"
    @mouseout="autoPlay"
  >
    <div
      class="carousel-box"
      :style="{
        width: allCount,
        '-webkit-transition': transitionConfig,
        '-webkit-transform': slateX
      }"
      ref="carousel"
    >
      <div
        class="carousel-item"
        v-for="(item, index) in imgList"
        :key="index"
        :style="{ '-webkit-transform': getImgLateX(index) }"
      >
        <img :src="item.img" />
      </div>
    </div>
    <span
      class="carousel-left"
      @click="toLeft"
    >&#8249;</span>
    <span
      class="carousel-right"
      @click="toRight"
    >&#8250;</span>
    <div
      class="carousel-dots"
      v-if="dots"
    >
      <button
        v-for="(item, index) in imgList.length"
        :key="index"
        :class="{ active: index == dotsIndex }"
        @click="toDots(index)"
      >
        <!-- {{ item }} -->
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "lbt",
  data() {
    return {
      imgList: [
        {
          img: require("../../../assets/img/enshi/马者茶园.jpeg")
        },
        {
          img: require("../../../assets/img/enshi/大岩阡梯田.jpeg")
        },
        {
          img: require("../../../assets/img/enshi/云龙地缝瀑布.jpeg")
        },
        {
          img: require("../../../assets/img/enshi/女儿湖.jpeg")
        }
      ],
      // 图片宽
      imgWidth: 390,
      // 指示器
      dots: true,
      arrow: true,
      // 初始播放位置
      initIndex: 0,
      // 自动播放
      auto: true,
      // 自动播放时间间隔
      autoTime: 4000,
      imgIndex: 0,
      durationTime: 0.4,
      dotsIndex: 0,
      autoer: null
    };
  },
  created() {
    this.imgIndex = this.initIndex;
    this.dotsIndex = this.initIndex;

    if (this.auto) this.autoPlay();
  },
  mounted() {
    this.imgWidth = document.body.offsetWidth;
  },
  computed: {
    allCount() {
      return this.imgList.length * this.imgWidth + "px";
    },
    slateX() {
      return "translate3d(" + -this.imgIndex * this.imgWidth + "px,0,0)";
    },
    transitionConfig() {
      return "all " + this.durationTime + "s";
    },
    imgLateX() {
      let width = -this.imgWidth;
      return "translate3d(" + width + "px,0,0)";
    },
    endImgLateX() {
      let width = this.imgList.length;
      return "translate3d(" + width + "px,0,0)";
    }
  },

  methods: {
    getImgLateX(i) {
      let width = this.imgWidth * i;
      return "translate3d(" + width + "px,0,0)";
    },
    toLeft() {
      this.imgIndex--;
      this.dotsIndex--;
      if (this.dotsIndex <= -1) this.dotsIndex = this.imgList.length - 1;

      if (this.imgIndex <= -1) this.loopFn("left");
    },
    toRight() {
      this.imgIndex++;
      this.dotsIndex++;
      if (this.dotsIndex == this.imgList.length) this.dotsIndex = 0;
      if (this.imgIndex == this.imgList.length) this.loopFn("right");
    },
    loopFn(type) {
      if (type == "right") this.imgIndex = 0;
      else this.imgIndex = this.imgList.length - 1;
    },
    toDots(index) {
      this.dotsIndex = index;
      this.imgIndex = index;
    },
    autoPlay() {
      if (this.auto) {
        clearInterval(this.autoer);
        this.autoer = setInterval(() => {
          this.toRight();
        }, this.autoTime);
      }
    },
    stopAuto() {
      if (this.auto) return clearInterval(this.autoer);
    }
  }
};
</script>

<style lang="less" scoped>
.carousel {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 200px;
  margin-bottom: 15px;
}
.carousel-left,
.carousel-right {
  position: absolute;
  cursor: pointer;
  top: 44%;
  display: inline-block;
  transform: translateY(-50%);
  color: rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  text-align: center;
  cursor: pointer;
  font-size: 60px;
  border-radius: 50%;
  transition: all 0.5s;
}
.carousel-left {
  left: 2%;
}
.carousel-right {
  right: 2%;
}
.carousel-left:hover,
.carousel-right:hover {
  color: rgba(255, 255, 255, 0.8);
}
.carousel-dots {
  position: absolute;
  bottom: 5%;
  text-align: center;
  left: 50%;
  transform: translateX(-50%);
}
.carousel-dots button {
  width: 10px;
  height: 10px;
  background: rgba(255, 255, 255, 0.5);
  display: inline-block;
  margin: 0 5px;
  border-radius: 50%;
  // color: #000;
  border: none;
  outline: none;
  transition: all 0.2s;
  cursor: pointer;
  font-size: 7px;
}
.carousel-dots button:hover,
.carousel-dots button.active {
  background: rgba(255, 255, 255, 0.8);
  // color: #fff;
}
.carousel-item {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  height: 200px;
}
.carousel-item img {
  border-radius: 10px;
  width: calc(100vw - 30px);
  height: 200px;
}
</style>
