import "@ktw/cesium/dist/Cesium/Widgets/widgets.css";
import "@ktw/ktw3d/dist/ktw3d/ktw3d.css";
import * as Cesium from "@ktw/cesium";
import * as ktw3d from "@ktw/ktw3d";

// import GPSController from '../assets/js/gps/GPSController';
export default class loadEarth {
  static initLoad(mapId, options) {
    if (!window.ktw3d) window.ktw3d = ktw3d;
    ktw3d.createMap({
      id: mapId,
      data: options,
      success: (viewer, data) => {
        this.initEarth(viewer);
        if (options.openFlyAnimation) {
          viewer.ktw.openFlyAnimation();
        }
      }
    });
  }

  static initEarth(viewer) {
    this.viewer = viewer;
  }
}
