<template>
  <div
    id="cesiumContainer"
    class="earth"
    :style="{ height: tabHeight }"
  ></div>
</template>

<script>
import loadEarth from "../../../../utils/loadEarth";
export default {
  props: {
    tabHeight: {
      type: String,
      default: "435px"
    }
  },
  data() {
    return {};
  },
  mounted() {
    this.$nextTick(() => {
      this.initMap();
    });
  },
  methods: {
    initMap() {
      loadEarth.initLoad("cesiumContainer", CONFIG_3D_MAP);
    }
  }
};
</script>

<style lang="less" scoped>
.earth {
  height: 100%;
  width: 100%;
  position: absolute;
}
</style>
