<template>
  <div
    class="topWeather"
    :style="{ height: tabHeight }"
  >
    <div class="weather-title">
      <h5>你好，欢迎来到 {{ cityName }} ~</h5>
    </div>

    <Row :gutter="16">
      <Col
        span="12"
        v-for="(item, index) in listWeather"
        :key="index"
      >
      <Row
        class="card-div"
        :gutter="4"
      >
        <Col :span="12">
        <Tag>{{ item.xq }}</Tag>
        <Tag>AQI {{ item.air_level }} </Tag>
        <Tag>{{ item.win[0] }} {{ item.win_speed }} </Tag>
        <Tag>{{ item.wea_day }}</Tag>
        </Col>
        <Col :span="12">
        <img
          :src="currentWeatherIcon(item)"
          height="50"
          width="50"
        />
        <div>↑{{ item.tem1 }} &nbsp; ↓{{ item.tem2 }}</div>
        <!-- <Tag>{{ item.tem }}</Tag> -->
        </Col>
      </Row>
      </Col>
    </Row>
  </div>
</template>

<script>
import { get7daysWeather } from "../../../../api/enshi";
export default {
  props: {
    tabHeight: {
      type: String,
      default: "435px"
    }
  },
  data() {
    return {
      cityName: "恩施",
      listWeather: [],
      iconObj: {
        晴: require("../../../../assets/img/weather/晴.svg"),
        多云: require("../../../../assets/img/weather/多云.svg"),
        阴: require("../../../../assets/img/weather/阴.svg"),
        小雨: require("../../../../assets/img/weather/小雨.svg"),
        中雨: require("../../../../assets/img/weather/中雨.svg"),
        大雨: require("../../../../assets/img/weather/大雨.svg")
      }
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.initWeather();
    });
  },
  computed: {
    currentWeatherIcon: function () {
      return function (item) {
        return this.iconObj[item.wea_day];
      };
    }
  },
  methods: {
    initWeather() {
      get7daysWeather(this.cityName)
        .then(res => {
          this.listWeather = res.data.data.map(item => {
            let day = item.day || "（星期一）";
            let arr = day.split("（");
            let xq = arr[1].split("）")[0];
            item.xq = xq;
            console.log(item.wea_day);
            return item;
          });
          console.log(this.listWeather);
        })
        .catch(erro => {
          throw erro;
        });
    }
  }
};
</script>

<style lang="less" scoped>
.topWeather {
  width: 100%;
  padding: 0 15px;
  background: #f5f7f9;
  overflow: auto;
}

.weather-title {
  margin-top: 20px;
  margin-bottom: 20px;
  width: 100%;
  background: #fff;
  padding: 10px;
  border-radius: 6px;
}

.card-div {
  width: 100%;
  height: 120px;
  padding: 10px;
  background: #fff;
  border-radius: 10px;
  margin-right: 10px;
  margin-bottom: 15px;
  font-size: 12px;
}
.size-10 {
  font-size: 20px;
  transform: scale(0.5); //通过 transform属性实现比例缩放
}
.size-08 {
  font-size: 20px;
  transform: scale(0.4); //通过 transform属性实现比例缩放
}
/deep/ .ivu-tag {
  padding: 0px 4px;
  height: 21px;
  line-height: 21px;
}
</style>
